import React, {useEffect, useState} from 'react';
import axios from "axios";
import './main.css'
import {Button, Dropdown, Menu, Spin, Table} from "antd";
import {useHistory} from "react-router-dom";
import {toast} from "react-toastify";

const Main = () => {
    const history=useHistory()
    const [loading,setLoading]=useState(false)
    const [tableLoad,setTableLoad]=useState(false)
    const [data,setData]=useState(null)
    useEffect(()=>{
        setLoading(true)
        axios.post('https://online-excel-heroku.herokuapp.com/student/list')
            .then((res)=>{
                mapData(res.data.data)
            })
            .finally(()=>{
                setLoading(false)
            })
    },[])
    const mapData = (res) => {
        const {data}=res
        const current=data.map((item,i)=>{
            return{
                key:item.id,
                id:item.id,
                name:item.fullName,
                university:item.universityName,
                entranceYear:item.entranceYear,
                graduationYear:item.graduationYear,
                dataIndex:item.dataIndex,
                faculty:item.faculty,
                speciality:item.speciality,
                studyType:item.studyType,
                academicType:item.academicType,
                diplomaSerial:item.diplomaSerial,
                diplomaRegistrationNumber:item.diplomaRegistrationNumber,
                givenDate:item.givenDate,
                academicLevel:item.academicLevel,
                appendixNumber:item.appendixNumber,
                organizationId:item.organizationId,

            }
        })
        setData(current)
    }
    const logOut=()=>{
        localStorage.clear()
        window.location.reload()
    }
    const columns = [
        {
            title: 'Harakat',
            dataIndex: 'id',
            render: (id) => (
                <div className='user-option'>
                    <ActionMenu id={id} deleteFunc={deleteFunc} />
                </div>
            ),
            onCell: () => {
                return {
                    onClick: (e) => e.stopPropagation()
                }
            }
        },
        {
            title: 'Id',
            dataIndex: 'key',
            sorter: {
                compare: (a, b) => a.key - b.key,
            }
        },
        {
            title: 'Student Name',
            dataIndex: 'name'
        },
        {
            title: 'University Name',
            dataIndex: 'university'
        },
        {
            title: 'Entrance Year',
            dataIndex: 'entranceYear'
        },
        {
            title: 'Graduation Year',
            dataIndex: 'graduationYear'
        },
        {
            title: 'Faculty',
            dataIndex: 'faculty'
        },
        {
            title: 'Speciality',
            dataIndex: 'speciality'
        },
        {
            title: 'Study Type',
            dataIndex: 'studyType'
        },
        {
            title: 'Academic Type',
            dataIndex: 'academicType'
        },
        {
            title: 'Diploma Serial',
            dataIndex: 'diplomaSerial'
        },
        {
            title: 'Diploma Registration Number',
            dataIndex: 'diplomaRegistrationNumber'
        },
        {
            title: 'Given Date',
            dataIndex: 'givenDate'
        },
        {
            title: 'Academic Level',
            dataIndex: 'academicLevel'
        },
        {
            title: 'Appendix Number',
            dataIndex: 'appendixNumber'
        },
        {
            title: 'Organization Id',
            dataIndex: 'organizationId'
        },

    ];
    const deleteFunc = (id) => {
        setTableLoad(true)
        axios.delete(`https://online-excel-heroku.herokuapp.com/student/delete/${id}`)
            .then((res)=>{
                const current =data.filter(item => item.id !==id)
                setData(current)
                toast.success("Student delete")
            })
            .finally(()=>{
                setTableLoad(false)
            })
    }
    return (
        <>
            {!loading &&<section className="main">
                <div className="header">
                    <h6>Welcome to Home page!</h6>
                    <Button onClick={logOut} type="primary">Log Out</Button>
                </div>
                <div className="info">
                    <Button className="add" onClick={()=>{history.push('/main/student/create')}} type="primary"><span className="plus">+</span>Add Student</Button>
                    <Table dataSource={data} loading={tableLoad} className="action pointer"
                           columns={columns}
                           scroll={{ x: 'max-content' }}
                           locale={{
                               emptyText: () => <div className='empty-table'>
                                   <img src="/assets/icons/empty-table.svg" alt="" />
                                   <span>Ma'lumot mavjud emas</span>
                               </div>,
                               triggerDesc: 'Tushirish',
                               triggerAsc: "Ko'tarish",
                               cancelSort: 'Bekor qilish',
                           }}
                           pagination={false}
                           onRow={(record, rowIndex) => {
                               return {
                                   onClick: (e) => history.push(`/teacher/inner/${record.id[0]}`)
                               }
                           }}
                    />
                </div>
            </section>}
            {
                loading && <div className="loader">
                    <Spin size={"large"}/>
                </div>
            }
        </>
    );
};

export default Main;

const ActionMenu = ({ id, deleteFunc}) => {

    const history = useHistory()

    const actionMenu = (
        <Menu>
            <Menu.Item onClick={() => history.push(`/main/student/update/${id}`)} key={1}>{"O'zgartirish"}</Menu.Item>
            <Menu.Item className='red' onClick={() => deleteFunc(id)} key={2}>{"O'chirish"}</Menu.Item>
        </Menu>
    );

    return (
        <Dropdown overlay={actionMenu} trigger={['click']} placement="bottomRight" >
            <dir className="option-btn">
               <h6 style={{marginBottom:"0"}}>...</h6>
            </dir>
        </Dropdown>
    )
}
