import React, {useState} from 'react';
import './auth.css'
import {Input, Button, Spin} from 'antd';
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input';
import axios from "axios";
import {toast} from "react-toastify";
import {useHistory} from "react-router-dom";

const Auth = () => {
    const history=useHistory()
    const [loading,setLoading]=useState(false)
    const [phone,setPhone]=useState('')
    const [fullName,setFullName]=useState('')
    const [username,setUserName]=useState('')
    const [password,setPassword]=useState('')
    const submit = (e) => {
      e.preventDefault()
        if (!phone && !password && !username && !fullName){
            toast.warning("Ba'zi ma'lumotlar kiritilmagan")
        }else {
            setLoading(true)
            axios.post('https://online-excel-heroku.herokuapp.com/auth/register',{
                fullName,
                username,
                password,
                phone
            })
                .then((res)=>{
                    if (res.data.status===200){
                        toast.success('Register done success')
                        localStorage.setItem('tokenId',res.data.data.data)
                        window.location.reload()
                    }else {
                        toast.error(res.data.data.error.message)
                    }
                })
                .catch((err)=>{
                    toast.error(err?.response)
                })
                .finally(()=>{
                    setLoading(false)
                })
        }


    }
    return (
        <section className="auth">
            <form onSubmit={submit}>
                <div className="card">
                        <h6>Register</h6>
                        <Input required value={fullName} onChange={(event => setFullName(event.target.value))} placeholder="Fullname" />
                        <Input required value={username} onChange={(event => setUserName(event.target.value))} placeholder="Username" />
                        <Input.Password
                            required
                            value={password}
                            onChange={(event => setPassword(event.target.value))}
                            placeholder="Password"
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                        />
                        <PhoneInput
                            required
                            international
                            defaultCountry={"UZ"}
                            value={phone}
                            onChange={setPhone} />
                        <Button disabled={loading} htmlType="submit" type="primary">{loading && <Spin/>}Sign In</Button>
                    <h6 onClick={()=>{history.push('/login')}} style={{marginTop:"20px"}}>Sizda akkaunt bormi?</h6>
                </div>
            </form>
        </section>
    );
};

export default Auth;
