import React, {useEffect, useState} from 'react';
import {Button, Input, Spin} from "antd";
import './action.css'
import {useHistory, useParams} from "react-router-dom";
import axios from "axios";
import {toast} from "react-toastify";

const Action = () => {
    const {id}=useParams()
    const history=useHistory()
    const [loading,setLoading]=useState(false)
    const [pageLoad,setPageLoad]=useState(false)
    const [fullName,setFullName]=useState('')
    const [universityName,setUniversityName]=useState('')
    const [entranceYear,setEntranceYear]=useState('')
    const [graduationYear,setGraduationYear]=useState('')
    const [faculty,setFaculty]=useState('')
    const [speciality,setSpeciality]=useState('')
    const [studyType,setStudyType]=useState('')
    const [academicType,setAcademicType]=useState('')
    const [diplomaSerial,setDiplomaSerial]=useState('')
    const [diplomaRegistrationNumber,setDiplomaRegistrationNumber]=useState('')
    const [givenDate,setGivenDate]=useState('')
    const [academicLevel,setAcademicLevel]=useState('')
    const [appendixNumber,setAppendixNumber]=useState('')
    const [organizationId,setOrganizationId]=useState('')
    useEffect(()=>{
        if (id){
            setPageLoad(true)
            axios.get(`https://online-excel-heroku.herokuapp.com/student/get/${id}`)
                .then((res)=>{
                    const {academicLevel,academicType,appendixNumber,diplomaRegistrationNumber,diplomaSerial,entranceYear,faculty,fullName,givenDate,graduationYear,id,organizationId,speciality,studyType,universityName}=(res.data.data.data)
                    console.log(res.data.data.data)
                    setAcademicLevel(academicLevel)
                    setAcademicType(academicType)
                    setAppendixNumber(appendixNumber)
                    setDiplomaRegistrationNumber(diplomaRegistrationNumber)
                    setDiplomaSerial(diplomaSerial)
                    setEntranceYear(entranceYear)
                    setFaculty(faculty)
                    setFullName(fullName)
                    setGivenDate(givenDate)
                    setGraduationYear(graduationYear)
                    setOrganizationId(organizationId)
                    setSpeciality(speciality)
                    setStudyType(studyType)
                    setUniversityName(universityName)
                })
                .finally(()=>{
                    setPageLoad(false)
                })
        }
    },[])
    const submit = (e) => {
      e.preventDefault()
      setLoading(true)
        const data={
            fullName,
            universityName,
            entranceYear,
            graduationYear,
            faculty,
            speciality,
            studyType,
            academicType,
            diplomaSerial,
            diplomaRegistrationNumber,
            givenDate,
            academicLevel,
            appendixNumber,
            organizationId:Number(organizationId),
            ...(id) && {id:Number(id)},
        }
        if (id){
            axios.post('https://online-excel-heroku.herokuapp.com/student/update',data)
                .then((res)=>{
                    if (res.data.data.success===true){
                        toast.success("Student edit")
                        history.push('/main')
                    }else {
                        toast.error(res.data.data.error.message)
                    }
                })
                .finally(()=>{
                    setLoading(false)
                })
        }else {
            axios.post('https://online-excel-heroku.herokuapp.com/student/create',data)
                .then((res)=>{
                    if (res.data.data.success===true){
                        toast.success("Student create")
                        history.push('/main')
                    }else {
                        toast.error(res.data.data.error.message)
                    }
                })
                .finally(()=>{
                    setLoading(false)
                })
        }

    }
    return (
        <>
            {!pageLoad && <section className='action'>
                <form onSubmit={submit}>
                <Input required value={fullName} onChange={(event => setFullName(event.target.value))} placeholder="Fullname" />
                <Input required value={universityName} onChange={(event => setUniversityName(event.target.value))} placeholder="University Name" />
                <Input required value={entranceYear} onChange={(event => setEntranceYear(event.target.value))} placeholder="Entrance Year" />
                <Input required value={graduationYear} onChange={(event => setGraduationYear(event.target.value))} placeholder="Graduation Year" />
                <Input required value={faculty} onChange={(event => setFaculty(event.target.value))} placeholder="Faculty" />
                <Input required value={speciality} onChange={(event => setSpeciality(event.target.value))} placeholder="Speciality" />
                <Input required value={studyType} onChange={(event => setStudyType(event.target.value))} placeholder="Study Type" />
                <Input required value={academicType} onChange={(event => setAcademicType(event.target.value))} placeholder="Academic Type" />
                <Input required value={diplomaSerial} onChange={(event => setDiplomaSerial(event.target.value))} placeholder="Diploma Serial" />
                <Input required value={diplomaRegistrationNumber} onChange={(event => setDiplomaRegistrationNumber(event.target.value))} placeholder="Diploma Registration Number" />
                <Input required value={givenDate} onChange={(event => setGivenDate(event.target.value))} placeholder="Given Date" />
                <Input required value={academicLevel} onChange={(event => setAcademicLevel(event.target.value))} placeholder="Academic Level" />
                <Input required value={appendixNumber} onChange={(event => setAppendixNumber(event.target.value))} placeholder="Appendix Number" />
                <Input type={"number"} required value={organizationId} onChange={(event => setOrganizationId(event.target.value))} placeholder="Organization Id" />
                <Button onClick={()=>{history.push('/main')}} className="no" htmlType={"button"} >Bekor qilish</Button>
                <Button disabled={loading} loading={loading} className="add" htmlType={"submit"} type="primary">{id?"Edit":"Add"}</Button>
                </form>
                </section>}
            {
                pageLoad && <div className="loader">
                    <Spin size={"large"}/>
                </div>
            }
        </>
    );
};

export default Action;
