import React, {useState} from 'react';
import {Button, Input, Spin} from "antd";
import {EyeInvisibleOutlined, EyeTwoTone} from "@ant-design/icons";
import {toast} from "react-toastify";
import axios from "axios";
import {useHistory} from "react-router-dom";

const Login = () => {
    const history=useHistory()
    const [loading,setLoading]=useState(false)
    const [username,setUserName]=useState('')
    const [password,setPassword]=useState('')
    const submit = (e) => {
      e.preventDefault()
      if (!username && !password){
          toast("Ma'lumotlarni to'liq kiriting")
      }else {
          setLoading(true)
          axios.post('https://online-excel-heroku.herokuapp.com/auth/token',{
              username,
              password
          })
             .then((res)=>{
                 if (res.data.data.data.accessToken){
                     localStorage.setItem('tokenApp',res.data.data.data.accessToken)
                     history.push('main')
                     window.location.reload()
                 }
             })
              .finally(()=>{
                  setLoading(false)
              })
      }
    }
    return (
        <section className="auth">
            <form onSubmit={submit}>
                <div className="card">
                    <h6>Login</h6>
                    <Input required value={username} onChange={(event => setUserName(event.target.value))} placeholder="Username" />
                    <Input.Password
                        required
                        value={password}
                        onChange={(event => setPassword(event.target.value))}
                        placeholder="Password"
                        iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    />
                    <Button disabled={loading} htmlType="submit" type="primary">{loading && <Spin/>}Login</Button>
                    <h6 onClick={()=>{history.push('/register')}} style={{marginTop:"20px"}}>Sizda akkaunt yo'qmi?</h6>
                </div>
            </form>
        </section>
    );
};

export default Login;
