import React, {useEffect, useState} from 'react';
import Auth from "./Components/Auth";
import {Redirect, Route, Switch, useHistory, useParams} from "react-router-dom";
import Login from "./Components/Login";
import axios from "axios";
import Main from "./Components/Main";
import Action from "./Components/StudentAction";

const App = () => {
    const history=useHistory()
    const link=useParams()
    const id=Number(localStorage.getItem('tokenId'))
    const token=localStorage.getItem('tokenApp')
    const [isAuth,setIsAuth]=useState(false)
    useEffect(()=>{
        if (id){
            axios.get(`https://online-excel-heroku.herokuapp.com/auth/get/${id}`)
                .then((res)=>{
                    if (res.data.data.data.id===id ){
                        setIsAuth(true)
                    }else{
                        history.push('/login')
                    }
                })
                .catch((err)=>{
                    history.push('/login')
                })
        }else if (token){
            setIsAuth(true)
        }else {
            setIsAuth(false)
            history.push('/login')
        }
    },[])
  return (
      <>
          <Switch>
              {!isAuth && <Route path={"/register"} component={Auth}/>}
              {!isAuth &&  <Route path={"/login"} component={Login}/>}
              {isAuth && <Route path={"/main"} component={Main} exact={true}/>}
              {isAuth && <Route path={"/main/student/create"} component={Action} exact={true}/>}
              {isAuth && <Route path={"/main/student/update/:id"} component={Action} exact={true}/>}
              {isAuth && <Redirect to={'/main'} exact={true} />}
          </Switch>
      </>
  );
};

export default App;
